import firebase from 'firebase'
import { db } from '../../main'
import * as _ from 'lodash'

const state = {
  chats: []
}

const getters = {
  chats: state => state.chats
}

const mutations = {
  'GET_CHATS': (state, payload) => state.chats = payload,
}

const actions = {
  getChats ({commit}) {
    db.collection('posts').
    commit('GET_CHATS', payload)
  },
  sendMessage ({commit, getters}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)

    const user = getters.user
    let imageURLs = []
    let fileURLs = []
    let key

    const chat = {
      senderId: user.id,
      recipientId: payload.recipientId,
      content: payload.content,
      createdAt: Date.now()
    }

    db.collection('chats')
      .add(chat)
      .then((doc) => {
        key = doc.id
        return db.doc(`chats${key}`).update({id: key})
      })
      .then(() => {
        let imagesIndex = _.range(payload.images.length)
        var metadata = {
          contentType: 'image/jpeg',
        }

        if (payload.images.length) {
          _.each(imagesIndex, (idx) => {
            var randImage = payload.images[idx]
            var storageRef = firebase.storage().ref('posts')
            var uploadTask = storageRef.child(`${key}/${randImage.name}`).put(randImage, metadata)

            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
              (snapshot) => {},
              (error) => {},
              () => {
                var downloadURL = uploadTask.snapshot.downloadURL
                imageURLs.unshift(downloadURL)
              })
          })
        }
      })
      .then(() => {
        let filesIndex = _.range(payload.files.length)
        if (payload.files.length) {
          _.each(filesIndex, (i) => {
            var randFile = payload.files[i]
            var storageRef = firebase.storage().ref('chats')
            var uploadTask = storageRef.child(`${key}/${randFile.name}`).put(randFile)

            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
              (snapshot) => {},
              (error) => {},
              () => {
                var downloadURL = uploadTask.snapshot.downloadURL
                fileURLs.unshift(downloadURL)
              })
          })
        }
      })
      .then(() => {
        return db.doc(`chats/${key}`).update({
          imageURLs,
          fileURLs
        })
      })
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
  },
  deleteMessage ({commit, getters}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)

    db.doc(`chats/${payload}`)
      .delete(payload)
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
  }
}

export default {
    state,
    getters,
    mutations,
    actions
}
