import firebase from 'firebase'
import { db } from '../../main'
import * as _ from 'lodash'

const state = {
  // posts: []
}

const getters = {
  // posts: state => state.posts
}

const mutations = {
  // 'GET_POSTS': (state, payload) => state.posts = payload,
}

const actions = {
  // getAllPosts ({commit}) {
  //   db.collection('posts').
  //   commit('GET_POSTS', payload)
  // },
  addPost ({commit, getters}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)

    const user = getters.user
    let imageURLs = []
    let key

    const post = {
      userId: user.id,
      content: payload.content,
      createdAt: Date.now(),
      sponsored: false,
      enabled: true,
      favoriteCount: 0,
      commentCount: 0,
      recentComments: [],
      retweetCount: 0
    }

    db.collection('posts').add(post)
      .then((doc) => {
        key = doc.id
        return db.doc(`posts${key}`).update({id: key})
      })
      .then(() => {
        let imagesIndex = _.range(payload.images.length)
        if (payload.images.length) {
          _.each(imagesIndex, (idx) => {
            var randImage = payload.images[idx]
            var storageRef = firebase.storage().ref('posts')
            var uploadTask = storageRef.child(`${key}/${randImage.name}`).put(randImage)

            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
              (snapshot) => {},
              (error) => {},
              () => {
                var downloadURL = uploadTask.snapshot.downloadURL
                imageURLs.unshift(downloadURL)
              })
          })
        }
      })
      .then(() => {
        if (imageURLs.length) {
          return db.doc(`posts/${key}`).update({
            imageURLs,
          })
        }
      })
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
  },
  updatePost ({commit, getters}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)

    let imageURLs = []

    db.doc(`posts/${payload.id}`)
      .update(payload)
      .then(() => {
        let imagesIndex = _.range(payload.images.length)
        if (payload.images.length) {
          _.each(imagesIndex, (idx) => {
            var randImage = payload.images[idx]
            var storageRef = firebase.storage().ref('posts')
            var uploadTask = storageRef.child(`${payload.id}/${randImage.name}`).put(randImage)

            uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
              (snapshot) => {},
              (error) => {},
              () => {
                var downloadURL = uploadTask.snapshot.downloadURL
                imageURLs.unshift(downloadURL)
              })
          })
        }
      })
      .then(() => {
        if (imageURLs.length) {
          return db.doc(`posts/${payload.id}`).update({
            imageURLs,
          })
        }
      })
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
  },
  deletePost ({commit, getters}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)

    db.doc(`posts/${payload}`)
      .delete(payload)
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch(error => {
        commit('SET_ERROR', error)
        commit('SET_LOADING', false)
      })
  }
}

export default {
    state,
    getters,
    mutations,
    actions
}
