<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'app',
  data: () => ({
    connectivity: false,
  }),
  computed: {
    ...mapGetters(['user']),
    userIsAuthenticated() {
      return this.user != null && this.user != undefined ? true : false
    },
    professionalAccount() {
      if (this.userIsAuthenticated) {
        return this.user.account == "professional" ? true : false
      }
      return false
    }
  },
  watch: {
    user (value) {
      if (value != null && value != undefined) {
        return this.getConnectivityState()
      }
      return
    },
    connectivity (value) {
      if (value == true) {
        return this.$store.dispatch('setUserPresence', {
          state: 'online'
        })
      } else {
        return this.$store.dispatch('setUserPresence', {
          state: 'offline'
        })
      }
      return
    }
  },
  methods: {
    getConnectivityState () {
      window.addEventListener('online', () => {
        this.connectivity = true
      })
      window.addEventListener('offline', () => {
        this.connectivity = false
      })
    }
  },
  created() {
    if (this.professionalAccount) {
      this.$store.dispatch('getUserPosition')
    }
    if (this.userIsAuthenticated) {
      this.getConnectivityState()
    }
  }
}
</script>

