import firebase from 'firebase'
import { db } from '../../main'

const state = {
  user: null,
}

const getters = {
  user: state => state.user,
}

const mutations = {
  SET_USER (state, payload) {
    return state.user = payload
  },
}

const actions = {
  getUserPosition ({commit, getters}, payload) {
    const user = getters.user
    let data = {}

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          data._geoloc.lat = position.coords.latitude
          data._geoloc.lng = position.coords.longitude

          db.doc(`users/${user.id}`).update(data)
        },
        (error) => {
          switch(error.code) {
            case error.PERMISSION_DENIED:
              this.error = this.language == 'fr' ? "Vous avez refusé la demande de Géolocalisation." : "You denied the request for Geolocation."
              break;
            case error.POSITION_UNAVAILABLE:
              this.error = this.language == 'fr' ? "Les informations de Géolocalisation sont indisponibles." : "Location information is unavailable."
              break;
            case error.TIMEOUT:
              this.error = this.language == 'fr' ? "La demande d'obtention de votre Géolocation a expiré." : "The request to get user location timed out."
              break;
            case error.UNKNOWN_ERROR:
              this.error = this.language == 'fr' ? "Une erreur inconnue est survenue." : "An unknown error occurred."
              break;
          }
      });
    } else {
      this.error = this.language == 'fr' ? "Votre Géolocation a échoué..." : "Your Geolocation failed."
    }
  },
  autoSignIn ({ commit }, payload) {
    commit('SET_USER', {
      id: payload.uid
    })
  },
  setUser ({ commit}, payload) {
      commit('SET_USER', payload)
  },
  getUser ({commit, getters}) {
    const userId = getters.user.id
    db.doc(`users/${userId}`)
      .onSnapshot((doc) => {
        if (doc && doc.exists) {
          const data = doc.data()
          // const user = {
          //   id: doc.id,
          //   ...doc.data()
          // }
          commit('SET_USER', data)
        }
      }, (error) => {
        console.error(error);
      })
  },
  updateUser ({commit, getters}, payload) {
    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)

    const user = getters.user
    let downloadURL = null

    let data = {}

    data.email = payload.email ? payload.email : ''
    data.firstName = payload.firstName ? payload.firstName : ''
    data.lastName = payload.lastName ? payload.lastName : ''
    data.avatar = payload.avatar ? payload.avatar : ''
    data.description = payload.description ? payload.description : ''
    data.company = payload.company ? payload.company : ''
    data.website = payload.website ? payload.website : ''
    data.twitter = payload.twitter ? payload.twitter : ''
    data.linkedin = payload.linkedin ? payload.linkedin : ''
    data.facebook = payload.facebook ? payload.facebook : ''
    data.youtube = payload.youtube ? payload.youtube : ''
    data.account = payload.account ? payload.account : 'basic'
    data.paymentPlan = payload.paymentPlan ? payload.paymentPlan : ''
    data.address = {
      address: payload.address.address ? payload.address.address : '',
      city: payload.address.city ? payload.address.city : '',
      state: payload.address.state ? payload.address.state : '',
      zip: payload.address.zip ? payload.address.zip : '',
      country: payload.address.country ? payload.address.country : '',
    }

    if (payload.account == 'professional') {
      data._geoloc = {
        lat: payload._geoloc.lat ? payload._geoloc.lat : '',
        lng: payload._geoloc.lng ? payload._geoloc.lng : '',
      }
    }
    //
    db.doc(`users/${user.id}`).update(data)
    .then(() => {
      const filename = payload.image.name
      const ext = filename.slice(filename.lastIndexOf('.'))
      if (payload.image != null) {
        downloadURL = firebase.storage().ref(`users/${user.id}.${ext}`).put(payload.image)
      }
    })
    .then(() => {
      let imageUrl = downloadURL.metadata.downloadURLs[0]
      if (downloadURL != null) {
        return db.doc(`users/${user.id}`).update({avatar: imageUrl})
      }
    })
    .then(() => {
      commit('SET_LOADING', false)
    })
    .catch((e) => {
      commit('SET_LOADING', false)
      console.error(e)
    })
  },
  signupWithEmail ({ commit, getters }, payload) {
    const language = getters.language

    commit('CLEAR_ERROR')
    commit('SET_LOADING', true)
    firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
    .then(user => {
      const data = {
        id: user.uid,
        createdAt: Date.now(),
        email: payload.email,
        enabled: true,
        language,
        account: 'basic'
      }
      db.doc(`users/${user.id}`).set(data, {merge: true})
      commit('SET_USER', data)
    })
    .then(() => {
      commit('SET_LOADING', false)
    })
    .catch((err) => {
      let errorCode;
      switch (err.code) {
        case 'auth/weak-password':
          errorCode = language == "fr" ? 'Le mot de passe doit avoir au-moins 6 charactères. Veuillez réessayer...' : "Password shoud have at least 6 characters. Please try again..."
          break;
        case 'auth/email-already-in-use':
          errorCode = language == "fr" ? 'L\'adresse email existe déjà sur un autre compte. Veuillez réessayer...' : "Email address already exists on another account. Please, try again..."
          break;
        case 'auth/invalid-email':
          errorCode = language == "fr" ? 'L\'adresse email n\'est pas valide. Veuillez réessayer...' : "Email address isn't valid. Please, try again..."
          break;
        case 'auth/network-request-failed':
          errorCode = language == "fr" ? 'Vérifiez votre connexion internet. Veuillez réessayer...' : 'Try to connect to the network. Please, try again...'
        default:
          errorCode = error.code
      }
      commit('SET_ERROR', errorCode)
      commit('SET_LOADING', false)
    })
  },
  loginWithEmail ({ commit, getters }, payload) {
    const language = getters.language

    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')

    firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
      .then(user => {
        const data = {
          id: user.uid,
        }
        commit('SET_USER', data)
        commit('SET_LOADING', false)
      })
      .catch(error => {
        let errorCode = ''
        switch (error.code) {
          case 'auth/user-not-found':
              errorCode = language == "fr" ? "Il n'y a pas d'utilisateur correspondant à cette adresse email. Veuillez réessayer..." : "User's not found for this email. Please, try again..."
              break;
          case 'auth/wrong-password':
              errorCode = language == "fr" ? "Le mot de passe est invalide. Veuillez réessayer..." : "Invalid password. Please, try again..."
              break;
          case 'auth/invalid-email':
              errorCode = language == "fr" ? "L'adresse email n'est pas valide. Veuillez réessayer..." : "Invalid ID. Please, try again..."
              break;
          case 'auth/network-request-failed':
              errorCode = language == "fr" ? 'Vérifiez votre connexion internet. Veuillez réessayer...' : 'Try to connect to the network. Please, try again...'
          default:
              errorCode = error.code
        }
        commit('SET_ERROR', errorCode)
        commit('SET_LOADING', false)
      })
  },
  loginWithGoogle ({ commit }) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')

    const provider = new firebase.auth.GoogleAuthProvider()
    firebase.auth().signInWithPopup(provider)
      .then(user => {
        const data = {
          id: user.uid,
          createdAt: Date.now(),
          email: user.email ? user.email : '',
          avatar: user.photoURL ? user.photoURL : '',
          enabled: true,
          language,
          account: 'basic'
        }
        db.doc(`users/${user.id}`).set(data, {merge: true})
        commit('SET_USER', data)
      })
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch((e) => {
        console.error(e);
      })
  },
  loginWithFacebook ({ commit }) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')

    const provider = new firebase.auth.FacebookAuthProvider()

    firebase.auth().signInWithPopup(provider)
      .then(user => {
        const data = {
          id: user.uid,
          createdAt: Date.now(),
          email: user.email ? user.email : '',
          avatar: user.photoURL ? user.photoURL : '',
          enabled: true,
          language,
          account: 'basic'
        }
        db.doc(`users/${user.id}`).set(data, {merge: true})
        commit('SET_USER', data)
      })
      .then(() => {
        commit('SET_LOADING', false)
      })
      .catch((e) => {
        console.error(e);
      })
  },
  logout ({ commit }) { // TO KEEP
    firebase.auth().signOut()
    commit('SET_USER', null)
  }
}

export default {
    state,
    getters,
    mutations,
    actions
}
